/*
* ==================================================
*
* HEADINGS
*
* ==================================================
*/

.cds3_hero {
    font-weight: 700 !important;
    font-size: 3.6rem !important;
    line-height: 42px !important;
    letter-spacing: -0.5px !important;
}

.cds3_heading {
    font-weight: 700 !important;
    font-size: 2.6rem !important;
    line-height: 32px !important;
    letter-spacing: -0.5px !important;
}

.cds3_subheading {
    font-weight: 500 !important;
    font-size: 2.0rem !important;
    line-height: 24px !important;
    letter-spacing: -0.25px !important;
}

.cds3_paragraph {
    font-weight: 400 !important;
    font-size: 1.8rem !important;
    line-height: 24px !important;
    letter-spacing: 0.25px !important;
}

.cds3_paragraphSmall {
    font-weight: 400 !important;
    font-size: 1.6rem !important;
    line-height: 24px !important;
    letter-spacing: 0.25px !important;
}

.cds3_caption {
    font-weight: 400 !important;
    font-size: 1.4rem !important;
    line-height: 18px !important;
    letter-spacing: 0px !important;
}

/*
* ==================================================
*
* Validation status
*
* ==================================================
*/

div.success > i{
    position: absolute;
    top: 42px;
    right: 6px;
    font-size: 1.8rem;
    color: #01A87F;
}

.infoInteractive > div.infoStep1 > i{
    position: absolute;
    top: 4px;
    right: 6px;
    font-size: 1.8rem;
    color: #087BB4;
    cursor: pointer;
}

div.error, div.info{
    font-size: 1.4rem;
    margin-top: 6px;
    
}

div.error > i, div.info > i {
    font-size: 1.8rem;
    margin-right: 3px;
}

div.error{
    color: #ED0722;
    text-align: left;
}

div.info{
    color: #087BB4;
    text-align: left;
}

div.info > i{
    color: #087BB4;
    text-align: left;
}